body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(150, 44, 245), rgb(46, 238, 139));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* #### Generated By: http://www.cufonfonts.com #### */

@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/RoboFriends/static/media/SEGA.12a3db8b.woff) format('woff');
}
h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 1;
    color: rgb(63, 235, 61);
    text-shadow: rgb(209, 50, 248) 0px 0px 0.05em, rgb(124, 14, 62) 0px 0px 0.2em, rgb(255, 53, 157) 0px 0px 0.3em
}

